<template>
    <div class="VideoListInfo">
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
            <el-tab-pane label="" name="first">
                <Table :columns="columns1" :data="data1" size="small" ref="table"></Table>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
                <Table :columns="columns2" :data="data2" size="small" ref="table"></Table>
            </el-tab-pane>
        </el-tabs>
        <Modal class="ShModel" 
            title=""
            v-model="flag"
            :closable="false">
            <p>ID</p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
        </Modal>
    </div>
</template>

<script>
export default {
  name: 'VideoListInfo',
  data () {
    return {
         flag: false,
         activeName: 'first',
         columns1:[
            {
                title: '',
                key: 'ztname'
            },
            {
                title: '',
                key: 'date',
                "sortable": true
            },
            {
                title: '',
                key: 'status',
                "sortable": true
            },
            {
                title: '',
                key: 'action',
                width: 150,
                align: 'center',
                render: (h, params) => {
                    return h('Button', {
                        on: {
                            click: () => {
                                this.flag = true;
                            }
                        }
                    }, '')
                }
            }
        ],
         columns2:[
            {
                title: '',
                key: 'videoname'
            },
            {
                title: '',
                key: 'date',
                "sortable": true
            },
            {
                title: '',
                key: 'status',
                "sortable": true
            },
            {
                title: '',
                key: 'action',
                width: 150,
                align: 'center',
                render: (h, params) => {
                    return h('Button', {
                        on: {
                            click: () => {
                                this.flag = true;
                            }
                        }
                    }, '')
                }
            }
        ],
         data1:[
            {
                ztname: '',
                videoname: '',
                date: '2021-08-06',
                status: 2
            },{
                ztname: '',
                videoname: '',
                date: '2021-08-06',
                status: 10
            }
        ],
         data2:[
            {
                ztname: '',
                videoname: '',
                date: '2021-08-06',
                status: 2
            },{
                ztname: '',
                videoname: '',
                date: '2021-08-06',
                status: 10
            }
        ],
    }
  },
  methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      }
  }
}
</script>

<style>
</style>



