<template>
    <div>
        <el-table
            :data="tableData"
            style="width: 100%;margin-bottom: 20px;"
            row-key="id"
            border
            default-expand-all
            :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
            <el-table-column
            prop="name"
            label=""
            width="200">
            </el-table-column>
            <el-table-column
            prop="date"
            label=""
            width="150">
            </el-table-column>
            <el-table-column
            prop="address"
            sortable
            width="100"
            label="">
            </el-table-column>
            <el-table-column
            prop="flag"
            sortable
            width="100"
            label="">
            </el-table-column>
            <el-table-column
            prop="date"
            label=""
            width="150">
            </el-table-column>
            <el-table-column
                label=""
                align="content">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
  name: 'userOpenVideo',
  data () {
    return {
        tableData: [{
          id: 1,
          date: '2016-05-02',
          name: 'TSL',
          address: '',
          flag: ''
        }, {
          id: 2,
          date: '2016-05-04',
          name: '',
          address: '',
          flag: ''
        }, {
          id: 3,
          date: '2016-05-01',
          name: '',
          address: '',
          flag: '',
          children: [{
              id: 31,
              date: '2016-05-01',
              name: '',
              address: '',
          flag: '',
              children: [{
                id: 311,
                date: '2016-05-01',
                name: '()',
                address: '',
                flag: '',
                }, {
                id: 312,
                date: '2016-05-01',
                name: '()',
                address: '',
                flag: ''
              }]
            }, {
              id: 32,
              date: '2016-05-01',
              name: '',
              address: '',
                flag: ''
          }]
        }, {
          id: 4,
          date: '2016-05-03',
          name: '',
          address: '',
                flag: ''
        },{
          id: 5,
          date: '2016-05-02',
          name: 'TSL',
          address: '',
          flag: ''
        }, {
          id: 6,
          date: '2016-05-04',
          name: '',
          address: '',
          flag: ''
        }, {
          id: 7,
          date: '2016-05-04',
          name: '',
          address: '',
          flag: ''
        }, {
          id: 8,
          date: '2016-05-04',
          name: '',
          address: '',
          flag: ''
        }, {
          id: 9,
          date: '2016-05-04',
          name: '',
          address: '',
          flag: ''
        } ]
    }
  },
  methods: {
      handleEdit(val1,val2){
          console.log(val1,val2);
          this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
      },
      handleDelete(val1,val2){
          console.log(val1,val2);
          this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
      }
  }
}
</script>

<style>
</style>



