/**/
export function int2time(s){
     var hour = parseInt(s/3600);
     var minute = parseInt((s % 3600)/60);
     var second = Math.ceil(s%60);
     if (second == 60){
          second = 0;
          minute += 1; 
     }
     if (minute == 60){
          minute = 0;
          hour += 1; 
     }
     if (hour<10) {
          if (hour<0) {
               hour = 0;
          }
          hour = "0"+hour;
     }
     if (minute<10) {
          if (minute<0) {
               minute = 0;
          }
          minute = "0"+minute;
     }
     if (second<10) {
          if (second<0) {
               second = 0;
          }
          second = "0"+second;
     }
     if (isNaN(second)) {
          return false;
     } else {
          return hour + ":" + minute + ":" + second;
     }
}


