<template>
    <div>
        <el-row>
            <el-col :span="20"><div class="grid-content bg-purple">
                
            </div></el-col>
            <el-col :span="4"><div class="grid-content bg-purple-light">
                <i class="el-icon-delete"></i>
                <i class="el-icon-plus"></i>
            </div></el-col>
        </el-row>
        <el-table
            :data="tableData"
            style="width: 100%;margin-bottom: 20px;"
            row-key="id"
            border
            default-expand-all
            :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
            <el-table-column
            type="selection"
            width="55">
            </el-table-column>
            <el-table-column
            prop="id"
            sortable
            width="100"
            label="">
            </el-table-column>
            <el-table-column
            prop="name"
            label=""
            width="200">
            </el-table-column>
            <el-table-column
            prop="date"
            label=""
            width="150">
            </el-table-column>
            <el-table-column
            prop="date"
            label=""
            width="150">
            </el-table-column>
            <el-table-column
            prop="username"
            label=""
            width="150">
            </el-table-column>
            <el-table-column
                label=""
                fixed="right"
                align="content">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button v-if="scope.row.id < 100"
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
  name: 'test',
  data () {
    return {
        tableData: [{
          id: 1,
          date: '2016-05-02',
          name: 'TSL',
          address: '',
          username:'test',
        }, {
          id: 2,
          date: '2016-05-04',
          name: '',
          address: '',
          username:'test',
        }, {
          id: 3,
          date: '2016-05-01',
          name: '',
          address: '',
          username:'test',
          children: [{
              id: 31,
              date: '2016-05-01',
              name: '',
              address: '',
          username:'test',
              children: [{
                id: 311,
                date: '2016-05-01',
                name: '()',
                address: '',
          username:'test',
                }, {
                id: 312,
                date: '2016-05-01',
                name: '()',
                address: '',
          username:'test8',
              }]
            }, {
              id: 32,
              date: '2016-05-01',
              name: '',
              address: '',
          username:'test7',
          }]
        }, {
          id: 4,
          date: '2016-05-03',
          name: '',
          address: '',
          username:'test6',
        },{
          id: 5,
          date: '2016-05-02',
          name: 'TSL',
          address: '',
          username:'test5',
        }, {
          id: 6,
          date: '2016-05-04',
          name: '',
          address: '',
          username:'test4',
        }, {
          id: 7,
          date: '2016-05-04',
          name: '',
          address: '',
          username:'test3',
        }, {
          id: 8,
          date: '2016-05-04',
          name: '',
          address: '',
          username:'test2',
        }, {
          id: 9,
          date: '2016-05-04',
          name: '',
          address: '',
          username:'test1',
        } ]
    }
  },
  methods: {
      handleEdit(val1,val2){
          console.log(val1,val2);
          this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
      },
      handleDelete(val1,val2){
          console.log(val1,val2);
          this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
      }
  }
}
</script>

<style>
</style>



