import axios from '../untils/http'
import QS from 'qs'
import base from './base'
import { offLineConfirm } from './au.js'
let ifWarnOffLine = true; //

//
function formatRequestData (data) {
    let _data = {}
    for (let n in data) {
        _data[n] = typeof data[n] === 'object' ? JSON.stringify(data[n]) : data[n];
    }
    return _data
}

/**
 * postpost
 * @desc
 * @param {String} url [url]
 * @param {Object} params []
 */
export function requestPostByUser(fun,data) {
    return axios({
        url: base+`/api/api.tsl`,
        method: 'post',
        data: QS.stringify({ __datafun__: fun, ...formatRequestData(data) })
    })
}
/**
 * getget
 * @desc
 * @param {String} url [url]
 * @param {Object} params []
 */
export function requestGetByUser() {
    return axios({
        url: `/api/api.tsl`,
        method: 'get'
    })
}
/***   ****/
