<template>
    <div class="UserListInfo">
      <el-row>
        <el-col :span="12"><div class="grid-content bg-purple search">
          <Input class="ts-search-noborder" :style="searchStyle" search clearable placeholder="..." />
        </div></el-col>
        <el-col :span="12"><div class="grid-content bg-purple-light">
          <el-checkbox-group v-model="checkList">
            <el-checkbox label=""></el-checkbox>
            <el-checkbox label=""></el-checkbox>
            <el-checkbox label="TS"></el-checkbox>
            <el-checkbox label="TS"></el-checkbox>
          </el-checkbox-group>
        </div></el-col>
      </el-row>
      <Table :columns="columns8" :data="data7" size="small" ref="table"></Table>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="1000">
      </el-pagination>
      <Modal class="ShModel" 
        title=""
        v-model="flag"
        :closable="false">
        <p>ID</p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p>ID</p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p>  </p>
      </Modal>
    </div>
</template>

<script>
export default {
  name: 'UserListInfo',
  components: {
  },
  data () {
    return {
      searchStyle: {
        width: '200px',
        marginLeft: '16px',
        marginTop: '3px'
      },
      checkList: [],
      columns8: [
          {
              "title": "",
              "key": "activity",
              "width": 150,
              "sortable": true,
              filters: [
                  {
                      label: '',
                      value: 1
                  },
                  {
                      label: '',
                      value: 2
                  }
              ],
              filterMultiple: false,
              filterMethod (value, row) {
                  if (value === 1) {
                      return row.show > 4000;
                  } else if (value === 2) {
                      return row.show < 4000;
                  }
              }
          },
          {
              "title": "USERID",
              "key": "uid",
              "width": 150,
          },
          {
              "title": "",
              "key": "uname",
              "width": 150,
          },
          {
              "title": "",
              "key": "company",
              "width": 165,
          },
          {
              "title": "",
              "key": "utype",
              "width": 150,
          },
          {
              "title": "ID",
              "key": "mid",
              "width": 150,
          },
          {
              title: '',
              key: 'action',
              width: 150,
              align: 'center',
              render: (h, params) => {
                  return h('Button', {
                      on: {
                          click: () => {
                            this.flag = true;
                          }
                      }
                  }, '')
              }
          }
      ],
      data7: [
        {
          activity: 1,
          uid: 'tswxh',
          uname: '',
          company: 'TS',
          utype: '',
          mid: 'ts',
          address: ' 1518 '
        }],
      flag: false,
    }
  },
  methods: {
      handleEdit(val1,val2){
        this.flag = true;
      }
  }
}
</script>

<style scopend lang="less">
.UserListInfo{
  .el-row{
    line-height: 33px;
    border-bottom: 1px solid #ddd;
    .search{
      text-align: left;
      .ts-search-noborder{
      }
    }
  }
}
</style>



