    handleClose(done){
      this.$refs.tsvideo.pause();
      done()
    },
    handleScroll(){
        let scrollTop = this.$refs.app.scrollTop
        if (scrollTop >= 240) {
          this.visible1 = true
        } else if(scrollTop > 0 && scrollTop < 240) {
          this.visible1 = false
        }
    }
  },
  mounted () {
    window.addEventListener('scroll',this.handleScroll,true)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
