<template>
    <div>
      <el-row>
        <el-col :span="12"><div class="grid-content bg-purple">
          <div id="main" style="width: 450px;height:300px;"></div>
        </div></el-col>
        <el-col :span="12"><div class="grid-content bg-purple-light">
          <div id="main1" style="width: 450px;height:300px;"></div>
        </div></el-col>
      </el-row>
      <el-row>
        <el-col :span="12"><div class="grid-content bg-purple">
          <div id="main2" style="width: 450px;height:300px;"></div>
        </div></el-col>
        <el-col :span="12"><div class="grid-content bg-purple-light">
          <div id="main3" style="width: 450px;height:300px;"></div>
        </div></el-col>
      </el-row>
    </div>
</template>

<script>
import echarts from 'echarts';
export default {
  name: 'Test2',
  data () {
    return {
    }
  },
  methods: {
    drawPie () {
      echarts.init(document.getElementById('main')).setOption({
        title: {
          text: '',
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          left: 'left',
        },
        series: [
            {
                name: '',
                type: 'pie',
                radius: '50%',
                data: [
                    {value: 1048, name: ''},
                    {value: 735, name: ''},
                    {value: 35, name: ''}
                ],
                emphasis: {
                    itemStyle: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }
        ]
      });
      echarts.init(document.getElementById('main1')).setOption({
          title: {
            text: '',
            left: 'center'
          },
          xAxis: {
              type: 'category',
              data: ['GUI()', '', '()', '()', '', 'BL()', '101()']
          },
          yAxis: {
              type: 'value'
          },
          series: [{
              data: [120, 200, 150, 80, 70, 110, 130],
              type: 'bar'
          }]
      });
      echarts.init(document.getElementById('main2')).setOption({
          title: {
            text: '',
            left: 'center'
          },
          xAxis: {
              type: 'category',
              data: ['GUI', '', '', '', '', 'BL', '101']
          },
          yAxis: {
              type: 'value'
          },
          series: [{
              data: [120, 200, 150, 80, 70, 110, 130],
              type: 'bar'
          }]
      });
      echarts.init(document.getElementById('main3')).setOption({
          title: {
            text: '',
            left: 'center'
          },
          xAxis: {
              type: 'category',
              data: ['', '', '', '', '', '']
          },
          yAxis: {
              type: 'value'
          },
          series: [{
              data: [120, 200, 150, 70, 110, 130],
              type: 'bar'
          }]
      });
    }
  },
  mounted(){
    this.$nextTick(function() {
      this.drawPie('main');
    });
  } 
}
</script>

<style>
</style>



